import crypto from 'crypto';
import ImageCompressor from 'html5-image-compress';

const md5Cache = {};

export function md5(content) {
  if (!md5Cache[content]) {
    md5Cache[content] = crypto.createHash('md5').update(content, 'utf-8').digest('hex');
  }
  return md5Cache[content];
}

const MIME_MAP = {
  'image/gif': 'gif',
  'image/jpg': 'jpg',
  'image/jpeg': 'jpg',
  'image/png': 'png',
};
export function genImageFileName(browserFile) {
  const key = browserFile.type + browserFile.name + browserFile.lastModified + browserFile.size;
  return `${md5(key)}.${MIME_MAP[browserFile.type]}`;
}

export function numToPx(num, fixedDigit = 2) {
  return `${parseFloat(num).toFixed(fixedDigit)}px`;
}

export function base64DataURItoBlob(dataURI) {
  // dataURI = 'data:image/png;base64,';
  const protocal = dataURI.split(',')[0];
  const byteString = atob(dataURI.split(',')[1]);
  // separate out the mime component
  const mimeString = protocal.split(':')[1].split(';')[0];
  // write the bytes of the string to a typed array
  const ia = new Uint8Array(byteString.length);
  for (let i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }
  return new Blob([ia], { type: mimeString });
}

export function compressImg(f) {
  return new Promise((resolve, reject) => {
    new ImageCompressor(f, {
      // before (file) {
      //   console.log(file, '...');
      // },
      done(file, base64DataURI) {
        resolve(base64DataURI);
      },
      fail(file) {
        console.log('...');
        reject();
      },
      notSupport() {
        alert('');
      },
    });
  });
}

export function getDomEventPath(e) {
  const ret = [];
  let target = e.target;
  while (target) {
    ret.push(target);
    target = target.parentNode;
  }
  return ret;
}

export function getImageNaturalSize(url) {
  // TODO 
  const img = new Image();
  img.src = url;
  return new Promise((resolve, reject) => {
    const check = function () {
      if (img.width > 0 || img.height > 0) {
        img.onload = null;
        img.onerror = null;
        resolve({
          width: img.width,
          height: img.height,
        });
        // img.src = '';
      } else {
        setTimeout(check, 40);
      }
    };
    img.onload = check;
    img.onerror = () => {
      img.onload = null;
      img.onerror = null;
      reject();
    };
    check();
  });
}

export function getUrlParam(name) {
  const query = window.location.search.substring(1);
  const pair = query.split('&');
  for (let i = 0; i < pair.length; i++) {
    const kvArr = pair[i].split('=');
    if (kvArr[0] === name) {
      return kvArr[1];
    }
  }
  return null;
}

export function isPhantom() {
  return window.callPhantom || window._phantom;
}



