      <map-render
        v-else
        :element="element"
        :scale-func="rem"
      ></map-render>
    </wrapper>
  </div>
</template>

<script>

  import Wrapper from './RenderWrapper';
  import MapRender from '../../editor/components/element/MapRender';
  import { rotateImgElemStyleFor4Viewer, rem } from '../../../helpers/style';

  export default {
    props: ['element'],
    components: {
      Wrapper,
      MapRender,
    },
    data() {
      return {
        captureMode: false,// TODO 
        coverImg: null,
      }
