import _ from 'lodash';

const remVal = val => `${(val || 0) / 64}rem`;
const remObj = (obj) => {
  const ret = {};
  Object.keys(obj).map(k => (ret[k] = remVal(obj[k])));
  return ret;
};
const remArr = val => val.map(remVal);

export function rem(val) {
  if (typeof val === 'object') {
    return remObj(val);
  } else if (Array.isArray(val)) {
    return remArr(val);
  }
  return remVal(val);
}

export function url(val) {
  return `url(${val})`;
}

export function deg(val) {
  return `rotate(${val || 0}deg)`;
}

export function percent(val) {
  return `${parseFloat(val) || 0}%`;
}

export function shadow(val, scaleFn) {
  const shadowPart = scaleFn ? scaleFn(val) : `${parseFloat(val) || 0}px`;
  return `black 0px 0px ${shadowPart}`;
}

export function calcBorderRadiusByStyle(style, scaleFn) {
  const val = style['border-radius'] / 200 * (Math.min(style.width, style.height));
  return scaleFn ? scaleFn(val) : val;
}

export function rotateBox({ top, left, width, height, rotate = 0 }) {
  let rotateVal = rotate % 180;
  if (rotateVal > 90) {
    rotateVal = 180 - rotateVal;
  }
  const calc = (a, b) =>
  (a * Math.cos(2 * Math.PI / 360 * rotateVal))
  + (b * Math.sin(2 * Math.PI / 360 * rotateVal));

  const roatedWidth = calc(width, height);
  const roatedHeight = calc(height, width);
  return {
    top: top - ((roatedHeight - height) / 2),
    left: left - ((roatedWidth - width) / 2),
    width: roatedWidth,
    height: roatedHeight,
  };
}

export function rotateElemLayout({ top, left, width, height, rotate = 0 }, scaleFunc) {
  return {
    width: scaleFunc(width),
    height: scaleFunc(height),
    marginLeft: scaleFunc(-width / 2),
    marginTop: scaleFunc(-height / 2),
    transform: deg(rotate),
  };
}

export const rotateElemLayout4Viewer = style => rotateElemLayout(style, rem);

export const rotateImgElemStyle = (style, scaleFunc) => {
  const layoutStyle = rotateElemLayout(style, scaleFunc);
  const extraStyle = {
    opacity: style.opacity,
    borderRadius: calcBorderRadiusByStyle(style, scaleFunc),
    boxShadow: shadow(style['box-shadow'], scaleFunc),
  };
  return _.merge(layoutStyle, extraStyle);
};

export const rotateImgElemStyleFor4Viewer = style => rotateImgElemStyle(style, rem);

export default {
  rem,
  url,
  deg,
  percent,
  shadow,
  rotateBox,
  rotateElemLayout,
  rotateElemLayout4Viewer,
};



