<template>
  <div id="canvas">
    <div class="content">
      <div
        class="elem-panel"
        :style="elemPanelStyle">
        <component
          v-for="item in elements"
          :is="item.type+'-element'"
          :element="item"
        >
        </component>
      </div>
    </div>
  </div>
</template>

<script>
  import Base from './Base';
  import TextElement from './TextElement';
  import ImgElement from './ImgElement';
  import MapElement from './MapElement'
  import LinkButtonElement from './LinkButtonElement'
  import { rem } from 'helpers/style';

  export default {
    mixins: [Base],
    props: ['elements', 'canvasSize'],
    components: {
      TextElement,
      ImgElement,
      MapElement,
      LinkButtonElement,
    },
    computed: {
      elemPanelStyle () {
        const { width, height } = this.canvasSize;
        return rem({ width, height })
      }
    }

  };
</script>

<style lang="scss" rel="stylesheet/scss">

  #canvas {
    position: relative;
    height: 100%;
    box-sizing: border-box;
    .loading {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: #fff;
      opacity: 0.5;
    }
    .content {
      position: relative;
      .elem-panel {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
      }
    }
    .bgArea {
      background-position: center center;
    }
    .loadingArea {
      position: absolute;
      top: 0;
      left: 0;
      background: #000;
      width: 100%;
    }
  }
</style>



