<template>
  <div class="link-button-element">
    <wrapper :element="element" class="link-button-wrapper">
      <link-button-render
        :scale-func="rem"
        :element="element"
        :style="renderStyle"></link-button-render>
    </wrapper>
  </div>
</template>

<script>
  import { rotateImgElemStyleFor4Viewer, rem } from '../../../helpers/style';
  import Wrapper from './RenderWrapper';
  import LinkButtonRender from '../../editor/components/element/LinkButtonRender';

  export default{
    props: ['element'],
    components: {
      Wrapper,
      LinkButtonRender,
    },
    computed: {
      imgSrc() {
        return this.element.picId;
      },
      renderStyle: function () {
        const { width, height } = this.element.style;
        return {
          marginLeft: rem(-width / 2),
          marginTop: rem(-height / 2),
        };
      },
    },
    methods: {
      rem,
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">

</style>



