import * as T from '../store/types';

const sqrtSquareSum = (a, b) => Math.sqrt((a * a) + (b * b));

export default {
  computed: {},
  methods: {
    // ab 
    vectorMulti(a, b) {
      const ra = sqrtSquareSum(a.x, a.y);
      const rb = sqrtSquareSum(b.x, b.y);
      const r = ((a.x * b.x) + (a.y * b.y)) / ra / rb;
      return parseInt(Math.acos(r) / Math.PI * 180, 10);
    },
    //  point  rotate  x 
    cos(point, rotate) {
      const deg = Math.abs(rotate);
      const { x, y } = point;
      return Math.cos(deg * Math.PI / 180) * sqrtSquareSum(x, y);
    },
    deg(rotate) {
      return `rotate(${rotate ? rotate : 0}deg)`;
    },
    scalePx(times) {
      return `${this.scale(times).toFixed(3)}px`;
    },
    boxShadow(v) {
      const val = parseFloat(v, 10);
      return val ? `black 0px 0px ${val}px` : 'none';
    },
    percent(val) {
      return `${Math.round(100 * val)}%`;
    },
    scale(size = 1) {
      return parseFloat(size * this.$store.getters[T.GET_SCALE_VAL]);
    },
    shadow(e = 0) {
      return "black 0px 0px " + parseFloat(e, 10) + "px";
    },
    getViewCenter() {
      const scale = this.scale(1);
      const canvasSize = this.$store.getters[T.GET_CANVAS_SIZE];
      const canvasWidth = canvasSize.width;
      const canvasHeight = canvasSize.height;
      const scaledCanvasHeight = canvasHeight * scale;
      const canvasCtOutHeight = $(".canvas-container").outerHeight();
      const editorOffsetTop = $(".editor").offset().top;
      const elemCanvasOffsetTop = $(".element-canvas").offset().top;
      //   
      const editorToCanvasTopGap = editorOffsetTop - elemCanvasOffsetTop;
      const left = canvasWidth / 2;

      const top = canvasCtOutHeight > scaledCanvasHeight //  > 
        // 
        ? canvasHeight / 2
        //  - (top padding)
        : ((canvasCtOutHeight / 2) + editorToCanvasTopGap) / scale;

      const canvasTop = elemCanvasOffsetTop >= editorOffsetTop // 
        ? 0
        : editorToCanvasTopGap / scale;

      // 
      const canvasBottom = scaledCanvasHeight - editorToCanvasTopGap > canvasCtOutHeight
        ? (editorToCanvasTopGap + canvasCtOutHeight) / scale
        : canvasHeight;

      return {
        canvasTop,
        canvasBottom,
        left,
        top,
      };
    },
    rotateBox(style) {
      const { top, left, width, height } = style;
      let { rotate = 0 } = style;

      const calc = (a, b) =>
      (a * Math.cos(2 * Math.PI / 360 * rotate))
      + (b * Math.sin(2 * Math.PI / 360 * rotate));

      rotate %= 180;
      if (rotate > 90) {
        rotate = 180 - rotate;
      }
      const roatedWidth = calc(width, height);
      const roatedHeight = calc(height, width);
      return {
        top: top - ((roatedHeight - height) / 2),
        left: left - ((roatedWidth - width) / 2),
        width: roatedWidth,
        height: roatedHeight,
      };
    },
  },
};



