<template>
  <div
    class="link-button-render"
    :style="calcStyle"
    :class="{'img-click-wrap':href}"
  >
    <img v-if="isImg" :src="imgUrl">
    <div v-else v-text="text" :style="textStyle" class="text"> </div>
    <a v-if="href" :href="href">a</a>
  </div>
</template>

<script>
  import Base from '../Base';

  export default {
    props: ['element', 'scaleFunc'],
    mixins: [Base],
    computed: {
      text(){

      },
      textStyle(){

      },
      href() {
        const { linkType, tel, url } = this.element;
        if (linkType === 'tel' && tel) {
          return `tel:${tel}`;
        } else if (linkType === 'link' && url) {
          return url.indexOf("http") === -1 ? `http://${url.trim()}` : url;
        }
        return null;
      },
      imgUrl() {
        return this.element.img;
      },
      isImg: function () {
        return this.element.display === 'img';
      },
      calcStyle: function () {
        const style = this.element.style;
        const { scaleFunc, boxShadow, deg } = this;
        return {
          'background-color': this.isImg ? 'transparent' : style['background-color'],
          height: scaleFunc(style.height),
          width: scaleFunc(style.width),
          opacity: style.opacity,
          'font-size': scaleFunc(style['font-size']),
          color: style.color,
          'border-radius': scaleFunc(style['border-radius']),
          'box-shadow': boxShadow(style['box-shadow']),
          '-webkit-transform': deg(style.rotate),
          transform: deg(style.rotate)
        };
      }
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .link-button-render {
    text-align: center;
    -webkit-transform-origin: center center;
    transform-origin: center center;
    position: absolute;
    left: 50%;
    top: 50%;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .img-click-wrap > a {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
  }
</style>



