<template>
  <div :style="opacityStyle" class="img-element">
    <wrapper :element="element" class="text-wrapper">
      <div :style="renderStyle" class="img-render" :class="{'img-click-wrap':element.href}">
        <img :src="imgSrc" />
        <a v-if="element.href" :href="element.href"></a>
      </div>
    </wrapper>
  </div>
</template>

<script>
  import { rotateImgElemStyleFor4Viewer, rem } from '../../../helpers/style';
  import Wrapper from './RenderWrapper';

  export default{
    props: ['element'],
    components: {
      Wrapper,
    },
    computed: {
      imgSrc() {
        return this.element.picId;
      },
      renderStyle() {
        return rotateImgElemStyleFor4Viewer(this.element.style);
      },
      opacityStyle: function () {
        return {
          opacity: this.element.style.opacity
        }
      },
    },
    methods: {
      rem: rem
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">

  .img-render {
    position: absolute;
    top: 50%;
    left: 50%;
    overflow: hidden;
    .loading {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.1);
      color: #fff;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }

  .border-image-container {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    position: absolute;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
</style>
<style>
  .img-click-wrap > a {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
  }
</style>



