<template>
  <div id="app">
    <v-canvas
      v-if="loaded"
      :elements="pageData.content"
      :canvas-size="pageData.canvasSize"
    ></v-canvas>
  </div>
</template>

<script>
  import _ from 'lodash';
  import vCanvas from './components/Canvas.vue';

  import {
    queryMeta,
    queryPageByMeta,
  } from 'api';
  import {
    getUrlParam,
  } from 'helpers';
  export default {
    components: {
      vCanvas,
    },
    data () {
      return {
        pageData: { // 
          content: null,
          canvasSize: null,
        },
        loaded: false,
      };
    },
    beforeCreate () {

    },
    created () {
      this.loadData();
      this.initBaseFontSzie();
      // TODO viewer model ( not pc && not phantom)
    },
    methods: {
      loadData() {
        const metaId = getUrlParam('id');
        if (!metaId) {
          return;
        }
        Promise.all([queryMeta(metaId), queryPageByMeta(metaId)])//
          .then(([meta, page]) => {
            _.merge(this.pageData, page.data);
            this.loaded = true;
          })
      },
      initBaseFontSzie() {
        var doc = window.document.documentElement;
        doc.style.fontSize = `${doc.getBoundingClientRect().width / 10}px`;
      }
    }
  };

</script>

<style lang="scss" rel="stylesheet/scss">
</style>



