<template>
  <div
    class="render-wrapper"
    :style="style">
    <slot/>
  </div>
</template>

<script>
  import { rem, rotateBox } from 'helpers/style';
  export default {
    props: ['element'],
    computed: {
      style() {
        return rem(rotateBox(this.element.style));
      }
    }
  };
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .render-wrapper {
    position: absolute;
    outline: none;
    z-index: 0;
  }
</style>



