<template>
  <div :style="renderStyle" class="map-render">
    <map-display :address="address" :location="location" :zoom="zoom"/>
    <div :style="naviStyle" class="navigation" @click="navi"></div>
    <div v-if="isEditor" class="map-cover">
    </div>
  </div>
</template>

<script>
  import Base from '../Base';
  const MAP_MIN_WIDTH = 600;
  import MapDisplay from './MapDisplay';
  import { MAP_APP_KEY, MAP_APP_NAME } from '../../../../constant';

  export default {
    mixins: [Base],
    props: ['element', 'scaleFunc', 'isEditor'],
    components: {
      MapDisplay,
    },
    computed: {
      address() {
        return this.element.map.address;
      },
      location() {
        return this.element.map.location;
      },
      zoom() {
        return this.element.map.zoom;
      },
      renderStyle: function () {
        const scaleFunc = this.scaleFunc;
        const { width, height } = this.element.style;
        return {
          width: scaleFunc(width),
          height: scaleFunc(height)
        };
      },
      naviStyle () {
        const scaleFunc = this.scaleFunc;
        const width = Math.max(MAP_MIN_WIDTH, this.element.style.width);
        return {
          width: scaleFunc(width / 8),
          height: scaleFunc(width / 10),
          lineHeight: scaleFunc(width / 10),
          right: scaleFunc(width / 60),
          bottom: scaleFunc(width / 60),
          fontSize: scaleFunc(width / 23),
        };
      },

    },
    methods: {
      navi() {
        const { location:{ lat, lng }, address } = this.element.map;
        let url = 'http://apis.map.qq.com/uri/v1/routeplan';
        url = `${url}?tocoord=${lat},${lng}&to=${address}&referer=${MAP_APP_NAME}`;
        window.open(url)
      },
      /*
       navigate: function () {
       var element = this.element,
       address = element.address,
       location = element.location,
       lat = location[0],
       lng = location[1],
       appKey = '52HBZ-FHLKX-O3543-7XLAA-U5HP3-B3BWN',
       s = 'http://apis.map.qq.com/tools/routeplan/eword='
       + address
       + '&epointx='
       + lng + '&epointy=' + lat + '?key=' + appKey;
       },
       */
    },
  };
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .map-render {
    position: absolute;
    background-repeat: no-repeat;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    .map-container {
      height: 100%;
    }
    .navigation {
      position: absolute;
      background-color: #18ccc0;
      text-align: center;
      color: #fff;
    }
    .map-cover {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
</style>

<style>
  a[title=""] {
    display: none !important;
  }
</style>



