<template>
  <div class="map-container">
  </div>
</template>

<script>
  export default {
    props: ['address', 'location', 'zoom'],
    methods: {
      updateZoom() {
        const { _map, zoom } = this;
        if (_map && zoom && _map.getZoom() !== zoom) {
          _map.setZoom(zoom);
        }
      },
      updateLocation() {
        const { _map, location } = this;
        if (_map && location) {
          const latLng = new qq.maps.LatLng(parseFloat(location.lat), parseFloat(location.lng));
          _map.setCenter(latLng);
          this._marker && this._marker.setMap(null);
          this._marker = new qq.maps.Marker({
            map: _map,
            animation: qq.maps.MarkerAnimation.DROP,
            position: latLng
          });
        }
      },
      createMap() {
        const map = this._map = new qq.maps.Map(this.$el, {
          disableDoubleClickZoom: true,
          draggable: false,
          zoom: this.zoom,
          disableDefaultUI: true
        });
        this.updateLocation();
      }
    },
    mounted() {
      this.createMap();
    },
    watch: {
      zoom() {
        console.log('>>>zoom change update map zoom');
        this.updateZoom()
      },
      location: {
        deep: true,
        handler: function () {
          console.log('>>>location change update map location');
          this.updateLocation()
        }
      },
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
