<template>
  <div class="text-render" :style="renderStyle">
    <div class="hack-scale-wrap"
         :style="scaleAreaStyle"
    >
      <div class="word-wrap"
           ref="editArea"
           @blur="onEditBlur"
           @keyup="onEditKeyUp"
           @keydown="onEditKeyDown"
           :class="{outline: isEditing}"
           :style="renderPStyle"
           :contenteditable="contenteditable"
      >
        <a :href="elementData.href" v-html="elementData.content"></a>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import Base from '../Base';
  var isChrome = /chrome|crios|crmo/i.test(window.navigator.userAgent);

  export default {
    mixins: [Base],
    props: [
      "element", // 
      "scaleFunc", // 
      "isEditor",
      "isEditing",
      "blurCb",
      "isEditing",
      "keyupCb",
      "keydownCb"
    ],
    computed: {
      contenteditable() {
        return this.isEditing ? "plaintext-only" : "false";
      },
      scaledFontSize() {
        return this.scaleFunc(this.elementData.style.fontSize);
      },
      elementData() {
        return this.element;
      },
      isVertical: function u() {
        return "vertical-lr" == this.elementData.style["writing-mode"];
      },
      needScale: function () {
        return isChrome && parseInt(this.scaledFontSize) < 12
          && this.scaledFontSize.indexOf("px") > 0
      },
      scaleTimes: function () {
        const times = parseFloat(this.scaledFontSize) / 12;
        return Math.min(times, 1)
      },
      scaleAreaStyle: function () {
        // chrome  <12px
        var style = this.elementData.style,
          scaleFunc = this.scaleFunc,
          scaleTimes = this.scaleTimes;
        return this.needScale ? {
          width: scaleFunc(style.width / scaleTimes),
          height: scaleFunc(style.height / scaleTimes),
          transform: "scale(" + scaleTimes + ")",
          transformOrigin: "0 0"
        } : {
          height: "100%"
        }
      },
      renderStyle: function () {
        var element = this.elementData,
          style = element.style,
          scaleFunc = this.scaleFunc,
          deg = this.deg;
        const ret = {
          width: scaleFunc(style.width),
          height: scaleFunc(style.height),
          lineHeight: style["line-height"],
          color: style.color,
          fontSize: this.needScale ? '12px' : this.cutRemTail(this.scaledFontSize),
          textAlign: style.textAlign,
          transform: deg(element.style.rotate),
          opacity: style.opacity,
          fontWeight: style["font-weight"],
          fontStyle: style["font-style"],
          textDecoration: style["text-decoration"]
        };
        return ret
      },
      renderPStyle: function () {
        const scaleFunc = this.scaleFunc,
          style = this.elementData.style,
          width = style.width,
          height = style.height,
          isVertical = this.isVertical,
          scaleTimes = this.needScale ? this.scaleTimes : 1;
        const writingMode = isVertical ? 'vertical-lr' : 'horizontal-tb';
        return {
          'writing-mode': writingMode,
          '-webkit-writing-mode': writingMode,
          width: isVertical ? 'auto' : scaleFunc(width / scaleTimes),
          height: isVertical ? scaleFunc(height / scaleTimes) : 'auto',
        };
      }
    },
    methods: {
      onEditBlur(e) {
        this.blurCb && this.blurCb(e);
      },
      onEditKeyUp (e) {
        this.keydownCb && this.keyupCb(e)
      },
      onEditKeyDown (e) {
        this.keydownCb && this.keydownCb(e)
      },
      cutRemTail (e) {
        var t = e.match(/(\d*\.\d{2})\d*rem/);
        return t && t[1] ? t[1] - .003 + "rem" : e
      }
    },
    watch: {
      isEditing (val) {
        if (val) {
          setTimeout(() => {
            var e = this.$refs.editArea
            if (window.getSelection) {
              const selection = window.getSelection();
              const range = document.createRange();
              range.selectNodeContents(e);
              selection.removeAllRanges();
              selection.addRange(range);
            }
            e.focus()
          }, 0);
        }
      }
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .text-element .text-render {
    position: absolute;
    top: 50%;
    left: 50%;
  }

  // .text-element .text-render 
  .text-render {
    transform-origin: 50% 50%;
    word-break: break-all;
    user-select: none;
    .word-wrap {
      white-space: pre-wrap;
      margin: 0;
      padding: 0;
      user-select: none;
      a {
        color: inherit;
        outline: none;
        text-decoration: none;
      }
    }
  }
</style>



