<template>
  <div class="text-element">
    <wrapper :element="element">
      <text-render
        :style="posStyle"
        :element="element"
        :scale-func="rem"
        class="text-render">
    </wrapper>
  </div>
</template>

<script>
  import { rotateElemLayout4Viewer, rem } from '../../../helpers/style';
  import Wrapper from './RenderWrapper';
  import TextRender from '../../editor/components/element/TextRender';

  export default {
    props: ['element'],
    components: {
      Wrapper,
      TextRender,
    },
    computed: {
      posStyle() {
        return rotateElemLayout4Viewer(this.element.style);
      }
    },
    methods: {
      rem: rem
    }
  };
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .text-render {
    position: absolute;
    top: 50%;
    left: 50%;
  }
</style>



